import React, { useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import classNames from "classnames";

import BillGates from '../images/BillGates.jpeg';
import JefBezos from '../images/JefBezos.jpeg';
import PonyMa from '../images/PonyMa.jpeg';
import JackMa from '../images/JackMa.jpeg';
import styles from './avatarSelector.module.scss'

const avatarDataList = [
  {
    name: 'BillGates',
    img: BillGates,
  },
  {
    name: 'JefBezos',
    img: JefBezos,
  },
  {
    name: 'PonyMa',
    img: PonyMa,
  },
  {
    name: 'JackMa',
    img: JackMa,
  }
]
export default function AvatarSelector() {
  const { t } = useTranslation();
  const [selected, setSelected] = useState(0);
  const [isSelect, setIsSelect] = useState(false);

  const selectedData = useMemo(()=> (
    avatarDataList[selected]
  ),[selected]);
  
  const getSelectedProfile = () => {
    return (
      <div className={styles.Profile}>
        <img
          src={selectedData.img}
          alt={selectedData.name}
          className={styles.ProfileImage}
          onClick={()=> {setIsSelect(true)}}
        />
        {t(`gameName.${selectedData.name}`)}
      </div>
      );
  }
  const getList = () => {
    return (
      <div className={styles.ProfileList}>
        { 
          avatarDataList.map((item, idx) => {
            return (
              <div key={item.name}>
                <img
                  src={item.img}
                  alt={item.name}
                  className={classNames(styles.ProfileImage, 
                      item.name === selectedData.name && styles.ProfileSelected)}
                  onClick={()=> {setSelected(idx); setIsSelect(false);}}
                />
                {t(item.name)}
              </div>
            )
          })
        }
      </div>
    );
  }
  return isSelect ? getList() : getSelectedProfile();
}