import React, { useState } from "react";
import { InputNumber } from "antd";
import { useTranslation } from "react-i18next";
import classNames from "classnames";
import PropTypes from 'prop-types';

import styles from "./ProductCell.module.scss";
import formatMoney from "../common/moneyFormatter";

function ProductCell({data, sell, trySpend}) {
  const { t } = useTranslation();
  const [index] = useState(data.index);

  const [count] = useState(data.buy_count);

  const _inputHandler = (event) => {
    if (event === "-") {
      return false;
    }
    if (typeof event == "string") {
      event = Number.parseInt(event);
    }
    let diff = event - count;
    if (diff > 0) {
      return _buy(diff);
    } else {
      return _sell(-diff);
    }
  };

  const _buy = (buy_count) => {
    trySpend(index, buy_count);
  };

  const _sell = (sell_count) => {
    sell(index, sell_count);
  };

  return (
    <div className={styles.ItemCell}>
      <img
        className={styles.ItemImage}
        src={data.image_src}
        alt={data.image_src}
        style={{ maxWidth: "100%", Height: "50%" }}
      ></img>
      <div className={styles.ItemName}>{t(data.product_name)}</div>
      <div className={styles.ItemCost}>${formatMoney(data.product_cost)}</div>
      <div className={styles.ItemControl}>
        <button
          className={classNames(
            styles[`ItemSell-${data.enable_sell ? "Enable" : "Disable"}`],
            styles.ItemSell
          )}
          onClick={(event) => {
            _sell(1);
          }}
          disabled={!data.enable_sell}
        >
          {t("Sell")}
        </button>
        <InputNumber
          onChange={_inputHandler}
          className={styles.ItemCount}
          type="number"
          pattern="\d*"
          defaultValue={0}
          controls={false}
          min={0}
          value={data.buy_count}
        />
        <button
          className={classNames(
            styles[`ItemBuy-${data.enable_buy ? "Enable" : "Disable"}`],
            styles.ItemBuy
          )}
          onClick={(event) => {
            _buy(1);
          }}
          disabled={!data.enable_buy}
        >
          {t("Buy")}
        </button>
      </div>
    </div>
  );
}

ProductCell.propTypes = {
  data: PropTypes.object,
  sell: PropTypes.func,
  trySpend: PropTypes.func,
};

export default ProductCell;
